@import "var.scss";

.section_topstory2{
    border-bottom: 4px solid rgba(34, 34, 34, 0.1);
    padding-bottom: 24px;
    &_left{
        width: 453px;
        .item-news{
            .title-news{font-size: 28px;margin-bottom: 15px;}
        }
    }
    &_right{
        width: 627px;
        width: calc(100% - 453px);
        position: relative;
        padding-left: 20px;
        &::before{
            content: "";
            border-left: 1px solid var(--border);
            position: absolute;
            top: 0;
            bottom: 0;
        }
        .item-news{
            width: 50%;padding-left: 20px;
            &:nth-child(1),&:nth-child(2){
                padding-bottom: 24px;
                margin-bottom: 24px;
                &::before{
                    content: "";
                    border-bottom: 1px solid var(--border);
                    position: absolute;
                    bottom: 0;
                    left: 20px;
                    right: 0;
                }
            }
        }
    }
    .gird_3{
        .item-news{
            .thumb-art{margin-bottom: 15px;}
            .title-news{font-size: 22px;}
            @media (max-width: 860px){
                .thumb-art{margin-bottom: 10px;}
                .title-news{font-size: 20px;}
            }
        }
    }
    @media (max-width: 860px){
        &_left{width: 40%;
            .item-news{
                .title-news{font-size: 22px;margin-bottom: 10px;}
            }
        }
        &_right{width: 60%;}
    }
}

.article_info {
    border-top: 1px solid var(--border);
    border-bottom: 1px solid var(--border);
    overflow: hidden;
    .gird{margin: 0 -15px;}
    .head_cate{margin-bottom: 12px;}
    .item{
        width: 50%;padding: 24px 15px;border-right: 1px solid var(--border);
        &:last-child{border-right:0}
    }
    .item-news{
        border-bottom: 0;margin-bottom: 0;padding-bottom: 0;
        .title-news{font-size: 20px;}
    }
    &_2{
        padding: 24px 0;
        .item{
            padding: 0 15px;
        }
    }
    &_3{
        border-top: 0;
        border-bottom: 4px solid rgba(34,34,34,0.1);
        padding-bottom: 45px;
        .item{
            padding: 0 15px;
            width: 299px;
            .title-news{font-size: 18px;}
            &:first-child{
                width: 476px;
                width: calc(100% - 299px);
                .title-news{font-size: 22px;}
            }
        }
    }
}

.box-img-style{
    background: #222222;padding: 40px 0;
    .item-news{
        .thumb-art{margin-bottom: 0;}
        .content{
            position: absolute;
            bottom: 0;
            left: 8px;
            right: 8px;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
            *{color: #fff;}
        }
        .title-news{
            font-size: 22px;margin-bottom: 0;
            a{
                padding: 80px 20px 15px 20px;display: block;
                @media (max-width: 1025px){
                    padding: 50px 15px 15px 15px;
                }
                @media (max-width: 860px){
                    padding: 30px 10px 10px 10px;
                }
            }
        }
        .status{
            padding: 0 20px 20px 20px;
            @media (max-width: 1025px){
                padding: 0 15px 15px 15px;
            }
            @media (max-width: 860px){
                padding: 0 10px 10px 10px;
            }
        }
        @media (max-width: 860px){
            .content{
                padding: 50% 10px 20px 10px;
            }
            .title-news{font-size: 18px;}
        }
    }
}

.box_quiz{
    border: 1px solid rgba(0, 0, 0, 0.1);border-radius: 8px;
    .head_cate {
        background: rgba(189, 189, 189, 0.2);
        padding: 24px 22px;
        justify-content: space-between;
        margin-bottom: 0;
        .social{
            a{
                display: inline-block;
                background: #E5E5E5;
                border-radius: 50%;
                width: 30px;
                height: 30px;
                text-align: center;
                line-height: 30px;margin-left: 5px;
                svg{color: #757575;}
            }
        }
    }
    .main-quiz{
        padding: 24px 57px;color: #4F4F4F;font-size:16px;
        .title{font: 600 22px var(--font1);color: #222222;margin-bottom: 15px;}
    }
    .gird{
        display: flex;flex-wrap: wrap;text-align: center;
        margin-top: 30px!important;
        .item{
            span{
                border: 1px solid #E5E5E5;background: #F7F7F7;
                width: 48px;height: 48px;line-height: 48px;text-align: center;
                display: inline-block;margin-top: 15px;border-radius: 50%;
            }
            &.active,&:hover{
                span{
                    background: #E5E5E5;
                }
            }
        }
    }
    .gird_1{
        .item{
            width: 100%;
        }
    }
    .gird_2{
        margin: 0 -8px;
        .item{
            width: 50%;padding: 0 8px;
        }
    }
    .gird_3{
        margin: 0 -8px;
        .item{
            width: 33.33%;padding: 0 8px;
        }
    }
    .gird_4{
        margin: 0 -8px;
        .item{
            width: 25%;padding: 0 8px;
        }
    }
    .answer-quiz{
        background: #F6F7FB;padding: 24px 57px;
        font-size: 16px;line-height: 1.4;
        margin-bottom: 24px;
        p{margin-top: 20px;}
    }
    .link-detail{
        padding: 29px 24px;
        background: #F7F7F7;
        border-radius: 0px 0px 8px 8px;
        display: block;
        position: relative;
        .next{
            background: #E5E5E5;
            border-radius: 0px 0px 8px 0px;
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            width: 57px;
            line-height: 77px;
            text-align: center;
            svg{
                width: 25px;height: 25px;
            }
        }
    }
}